<template>
  <Form @submit="Click" v-slot="{ errors }">
  <Field name="email" rules="validations"/>
    <span>{{errors.email}}</span>
  <Field name="password" rules="validations"/>
    <span>{{errors.password}}</span>
  </Form>


  <button v-if="this.title==='로그인'" @click="ChangeUrl">회원가입</button>
  <button v-if="this.title==='회원가입'" @click="ChangeUrl">로그인</button>
  <button @click="this.$router.push({path: '/'});">홈</button>

</template>

<script>
import axios from 'axios'
import {Form, Field} from 'vee-validate';

const resourceHost = "http://localhost:9000/api/v1"
export default {
  name: 'Login',
  components: {
    Form,
    Field,
  },
  data: () => ({
    validation: {alpha_dash : true},
    title: '로그인',
    email: '',
    password: '',
    url: '',
    result: '',
    userModel: {
      email: '',
      password: '',
      authority: 'ROLE_USER',
    },
    errors: [],
  }),
  state: {
    accessToken: null,
  },
  getters: {},
  mutations: {
    login(state, {accessToken}) {
      state.accessToken = accessToken
      localStorage.accessToken = accessToken
    },
  },
  methods: {
    Click(){
      if(this.title == '로그인')
        this.Login();
      else
        this.SignUp();
    },

    Login() {
      if (this.$cookies.get('sessionId') != null) {
        this.url = `${resourceHost}/login/check?id=${this.$cookies.get('sessionId')}`
      } else {
        if (!this.email || !this.password) {
          alert("Your email or password is empty.");
        }
        this.url = `${resourceHost}/login?email=${this.email}&password=${this.password}`
      }
      return axios
          .get(this.url)
          .then(({data}) => {
            console.log(data);
            if (this.$cookies.get('sessionId') == null) {
              this.$cookies.set('sessionId', data.sessionId);
              this.$cookies.set('email', data.username);
            }
            this.$router.push({path: '/'});

          })
          .catch(() => {
            this.result = "로그인에 실패했습니다. 다시 시도해주세요"
            if (this.$cookies.get('sessionId') != null)
              this.$cookies.remove('sessionId');
          })
    },

    ChangeUrl(){
      if(this.title == '로그인') {
        this.title = '회원가입';
        this.url = resourceHost + '/user';
      }else {
        this.title = '로그인';
      }
    },

    SignUp(){


      this.userModel.email = this.email;
      this.userModel.password = this.password;

      return axios
          .post(this.url, this.userModel)
          .then(({data}) => {
            console.log(data);


          })
          .catch(() => {

          })
    },
    CheckEmail(){
      if (this.email && this.password) {
        return true;
      }
      if(!this.email) this.errors[0].push("이메일을 입력해주세요");
    },
    CheckPassword(){
      const reg = /^[a-zA-Z0-9]*$/;
      if(!reg.exec(this.password)){
        this.errors[1].push("비밀번호는 숫자 영어조합만 가능합니다.")
      }
      if(!this.password) this.errors[1].push("비밀번호를 입력해주세요");
    }
  },

}
</script>
